import React from 'react';
import '../App.scss'
// Components
import Nav from '../component/Nav';
import Body from '../component/Body';
import Button from '../component/Button';
import { useNavigate } from "react-router-dom";

const Riding: React.FC = () => {
    const move_page = useNavigate();

    // Finish Riding
    const finish_riding = () => {
        window.alert("  .");
        move_page('/');
    }

    return (
        <div className="page_riding">
            {/* Navigator */}
            <div>
                <Nav nav_props={{ title: "", show_x: false, show_logo: true}} />
            </div>
            {/* Body */}
            <div>
                <Body title="..." />
            </div>
            {/* Footer */}
            <div className="sect_btn">
                <Button title=" " type={{ is_error: true, is_activated: false }} isButtonActivated={true}  onClick={finish_riding} />
            </div>
        </div>
    );
};

export default Riding;